.button {
  border: none;
  background: #ff6a9f;
  display: flex;
  align-items: center;
  padding: 8px 30px 8px 10px;
  gap: 10px;
  border-radius: 50px;
  cursor: pointer;
  z-index: 1;
  min-width: 276px;
}

.button_type_absolute {
  position: absolute;
  bottom: 0;
  left: 574px;
  bottom: 33px;
}

.button__arrow {
  position: relative;
  background: #000;
  padding: 15px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  z-index: 100;
}

.button__text {
  letter-spacing: 1px;
  font-size: 26px;
}

.button__arrow-box {
  position: relative;
  width: 63px;
  height: 63px;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.button__arrow-box::before {
  content: "";
  position: absolute;
  top: 2px;
  bottom: 2px;
  left: 2px;
  right: 2px;
  background: linear-gradient(
    120deg,
    rgba(174, 77, 116, 0.8) 0%,
    rgba(255, 106, 159, 1) 50%,
    rgba(255, 165, 194, 0.8) 100%
  );
  border-radius: 50px;
  z-index: 0;
}
